import { AccountRoleCode } from '../enums/AccountRoleCode';
import { UserTypeCode } from '../enums/UserTypeCode';

/* 
 *  User registration data model
*/
export class UserRegistration {
 
    // User name 
    public name: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // Default account Id 
    public accountId: string;
 
    // List of accounts and roles the user has access to 
    public accountRole: AccountRoleCode;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    public type: UserTypeCode;
 
    // Temporary password (leave empty for system generated password) 
    public tempPassword: string;
 
    // User must change password on next login 
    public changePassword: boolean;
 
    // Flag to send email verification to the user 
    public verifyByEmail: boolean;
 
    // User description 
    public description: string;
 
    constructor(name?: string, email?: string, mobile?: string, accountId?: string, accountRole?: AccountRoleCode, type?: UserTypeCode, tempPassword?: string, changePassword?: boolean, verifyByEmail?: boolean, description?: string) { 
        this.name = name;
        this.email = email;
        this.mobile = mobile;
        this.accountId = accountId;
        this.accountRole = accountRole;
        this.type = type;
        this.tempPassword = tempPassword;
        this.changePassword = changePassword;
        this.verifyByEmail = verifyByEmail;
        this.description = description;
    }

}


/*
 *  Interface of UserRegistration
*/
export interface IUserRegistration {
 
    // User name 
    name?: string;
 
    // User email 
    email?: string;
 
    // User mobile phone number (for notification and validation) 
    mobile?: string;
 
    // Default account Id 
    accountId?: string;
 
    // List of accounts and roles the user has access to 
    accountRole?: AccountRoleCode;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    type?: UserTypeCode;
 
    // Temporary password (leave empty for system generated password) 
    tempPassword?: string;
 
    // User must change password on next login 
    changePassword?: boolean;
 
    // Flag to send email verification to the user 
    verifyByEmail?: boolean;
 
    // User description 
    description?: string;
 
}
