
/* 
 *  Entity action response message returned for any create/update action on entity
*/
export class ActionResponse {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 
    // Entity key 
    public key: string;
 
    // Additional data 
    public data: string;
 
    constructor(code?: number, error?: string, key?: string, data?: string) { 
        this.code = code;
        this.error = error;
        this.key = key;
        this.data = data;
    }

}


/*
 *  Interface of ActionResponse
*/
export interface IActionResponse {
 
    // Error code (0 for success) 
    code?: number;
 
    // Error message 
    error?: string;
 
    // Entity key 
    key?: string;
 
    // Additional data 
    data?: string;
 
}
