
/* 
*/
export class UserTokenRequest {
 
    // User Id 
    public id: string;
 
    // Expiration time [Epoch milliseconds Timestamp] 
    public exp: number;
 
    constructor(id?: string, exp?: number) { 
        this.id = id;
        this.exp = exp;
    }

}


/*
 *  Interface of UserTokenRequest
*/
export interface IUserTokenRequest {
 
    // User Id 
    id?: string;
 
    // Expiration time [Epoch milliseconds Timestamp] 
    exp?: number;
 
}
