import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Incident
*/
export class Incident extends BaseEntity {
 
    // Resource Id (in the club) 
    public resourceId: string;
 
    // List of involved users 
    public involved: string[];
 
    // When the incident occurred [Epoch milliseconds Timestamp] 
    public occurredOn: number;
 
    // Incident description 
    public description: string;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Incident
*/
export interface IIncident extends IBaseEntity {
 
    // Resource Id (in the club) 
    resourceId?: string;
 
    // List of involved users 
    involved?: string[];
 
    // When the incident occurred [Epoch milliseconds Timestamp] 
    occurredOn?: number;
 
    // Incident description 
    description?: string;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
