
/* 
 *  Key Value string tuple
*/
export class StringKeyValue {
 
    // Key of tuple 
    public key: string;
 
    // Value of tuple 
    public value: string;
 
    constructor(key?: string, value?: string) { 
        this.key = key;
        this.value = value;
    }

}


/*
 *  Interface of StringKeyValue
*/
export interface IStringKeyValue {
 
    // Key of tuple 
    key?: string;
 
    // Value of tuple 
    value?: string;
 
}
