import { Resource } from '../entities/Resource';
import { EntityResponse } from '../messages/EntityResponse';
import { IEntityResponse } from '../messages/EntityResponse';

/* 
*/
export class EntityResponseOfResource extends EntityResponse {
 
    // Resource 
    public entity: Resource;
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 

}


/*
 *  Interface of EntityResponseOfResource
*/
export interface IEntityResponseOfResource extends IEntityResponse {
 
    // Resource 
    entity?: Resource;
 
    // Error code (0 for success) 
    code?: number;
 
    // Error message 
    error?: string;
 
}
