import { FeatureCode } from '../enums/FeatureCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  System functionality (feature) description
*/
export class Feature extends BaseEntity {
 
    // Feature code 
    public code: FeatureCode;
 
    // Feature name 
    public name: string;
 
    // Feature description 
    public description: string;
 
    // Feature category 
    public category: string;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Feature
*/
export interface IFeature extends IBaseEntity {
 
    // Feature code 
    code?: FeatureCode;
 
    // Feature name 
    name?: string;
 
    // Feature description 
    description?: string;
 
    // Feature category 
    category?: string;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
