import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { UserInvitation } from '../common/UserInvitation';
import { Member } from '../entities/Member';
import { AccountRoleCode } from '../enums/AccountRoleCode';
import { MemberStatusCode } from '../enums/MemberStatusCode';


/**
 * List of all user related actions for account administrator only 
 */
@Injectable()
export class AdminMembersService {

  // URL to web api
  private baseUrl = '/admin/members';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Send invitation to a new member for the current account
   * @Return: ActionResponse
   */
  invite(body?: UserInvitation) {
    return this.rest.post(`${this.baseUrl}/invite`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Resend invitation to an existing member for the current account
   * @Return: ActionResponse
   */
  reInvite(id?: string) {
    return this.rest.post(`${this.baseUrl}/re-invite/${id}`, null);
  }

  /**
   * Update member
   * @Return: EntityResponse<Member>
   */
  update(body?: Member) {
    return this.rest.put(`${this.baseUrl}/{id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete member from the account
   * The member will be removed from the account, if no other memberships exist for the user, it will be deleted from the system
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single member by id (including user data)
   * @Return: EntityResponse<MemberUser>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get user memberships (in all accounts)
   * @Return: QueryResponse<Membership>
   */
  getUserMemberships(userId?: string) {
    return this.rest.get(`${this.baseUrl}/memberships/${userId}`);
  }

  /**
   * Find list of users and filter the list
   * System user will see all users, Account system will see all users of the account, registered user will get an error.
   * @Return: QueryResponse<MemberUser>
   */
  find(accountId?: string, search?: string, role?: AccountRoleCode[], status?: MemberStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (role != null) { params.push(`role=${role}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
