import { MemberStatusCode } from '../enums/MemberStatusCode';
import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';
import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
 *  Login data (returned by the API after successful login)
*/
export class LoginData {
 
    // Access token to use (for the default account) 
    public accessToken: string;
 
    // User Id 
    public userId: string;
 
    // Account Id 
    public accountId: string;
 
    // Current account id and role 
    public accountRole: AccountRoleCode;
 
    // Member status: UNDEFINED | PENDING | ACTIVE | FROZEN 
    public memberStatus: MemberStatusCode;
 
    // User name 
    public userName: string;
 
    // User email 
    public userEmail: string;
 
    // User type 
    public userType: UserTypeCode;
 
    // User status 
    public userStatus: UserStatusCode;
 
    // User must change password on next login 
    public changePassword: boolean;
 
    constructor(accessToken?: string, userId?: string, accountId?: string, accountRole?: AccountRoleCode, memberStatus?: MemberStatusCode, userName?: string, userEmail?: string, userType?: UserTypeCode, userStatus?: UserStatusCode, changePassword?: boolean) { 
        this.accessToken = accessToken;
        this.userId = userId;
        this.accountId = accountId;
        this.accountRole = accountRole;
        this.memberStatus = memberStatus;
        this.userName = userName;
        this.userEmail = userEmail;
        this.userType = userType;
        this.userStatus = userStatus;
        this.changePassword = changePassword;
    }

}


/*
 *  Interface of LoginData
*/
export interface ILoginData {
 
    // Access token to use (for the default account) 
    accessToken?: string;
 
    // User Id 
    userId?: string;
 
    // Account Id 
    accountId?: string;
 
    // Current account id and role 
    accountRole?: AccountRoleCode;
 
    // Member status: UNDEFINED | PENDING | ACTIVE | FROZEN 
    memberStatus?: MemberStatusCode;
 
    // User name 
    userName?: string;
 
    // User email 
    userEmail?: string;
 
    // User type 
    userType?: UserTypeCode;
 
    // User status 
    userStatus?: UserStatusCode;
 
    // User must change password on next login 
    changePassword?: boolean;
 
}
