import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Verification type used to verify user with a temporary code
*/
export class Verification extends BaseEntity {
 
    // Verification code 
    public code: string;
 
    // Verification for user id 
    public userId: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // When the verification code was sent [Epoch milliseconds Timestamp] 
    public sent: number;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Verification
*/
export interface IVerification extends IBaseEntity {
 
    // Verification code 
    code?: string;
 
    // Verification for user id 
    userId?: string;
 
    // User email 
    email?: string;
 
    // User mobile phone number (for notification and validation) 
    mobile?: string;
 
    // When the verification code was sent [Epoch milliseconds Timestamp] 
    sent?: number;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
