import { BaseEntity } from '../entities/BaseEntity';
import { FeatureCode } from '../enums/FeatureCode';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Group of features
*/
export class FeaturesGroup extends BaseEntity {
 
    // Features group name 
    public name: string;
 
    // List of associated features 
    public features: FeatureCode[];
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of FeaturesGroup
*/
export interface IFeaturesGroup extends IBaseEntity {
 
    // Features group name 
    name?: string;
 
    // List of associated features 
    features?: FeatureCode[];
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
