
/* 
 *  Entities response message returned for read operation on multiple entities
*/
export class EntitiesResponse {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 
    constructor(code?: number, error?: string) { 
        this.code = code;
        this.error = error;
    }

}


/*
 *  Interface of EntitiesResponse
*/
export interface IEntitiesResponse {
 
    // Error code (0 for success) 
    code?: number;
 
    // Error message 
    error?: string;
 
}
