
/* 
 *  Weight Range
*/
export class WeightRange {
 
    // Minimum weight [Kg] 
    public min: number;
 
    // Maximum weight [Kg] 
    public max: number;
 
    constructor(min?: number, max?: number) { 
        this.min = min;
        this.max = max;
    }

}


/*
 *  Interface of WeightRange
*/
export interface IWeightRange {
 
    // Minimum weight [Kg] 
    min?: number;
 
    // Maximum weight [Kg] 
    max?: number;
 
}
