import { AccountRoleCode } from '../enums/AccountRoleCode';
import { MemberStatusCode } from '../enums/MemberStatusCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  Member in an account with extended account info (for display only)
*/
export class Membership extends BaseEntity {
 
    // User Id 
    public userId: string;
 
    // Account Id 
    public accountId: string;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    public accountRole: AccountRoleCode;
 
    // Member status: UNDEFINED | PENDING | ACTIVE | FROZEN 
    public status: MemberStatusCode;
 
    // Member in the club since [Epoch milliseconds Timestamp] 
    public memberSince: number;
 
    // Membership expiration [Epoch milliseconds Timestamp] 
    public expiration: number;
 
    // Member level [1,2,3] 
    public level: number;
 
    // Member can be cox ? 
    public isCox: boolean;
 
    // Member can be mentor ? 
    public isMentor: boolean;
 
    // Training time [minutes] 
    public trainingTime: number;
 
    // Restrictions 
    public restrictions: string;
 
    // Account Name 
    public accountName: string;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of Membership
*/
export interface IMembership extends IBaseEntity {
 
    // User Id 
    userId?: string;
 
    // Account Id 
    accountId?: string;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    accountRole?: AccountRoleCode;
 
    // Member status: UNDEFINED | PENDING | ACTIVE | FROZEN 
    status?: MemberStatusCode;
 
    // Member in the club since [Epoch milliseconds Timestamp] 
    memberSince?: number;
 
    // Membership expiration [Epoch milliseconds Timestamp] 
    expiration?: number;
 
    // Member level [1,2,3] 
    level?: number;
 
    // Member can be cox ? 
    isCox?: boolean;
 
    // Member can be mentor ? 
    isMentor?: boolean;
 
    // Training time [minutes] 
    trainingTime?: number;
 
    // Restrictions 
    restrictions?: string;
 
    // Account Name 
    accountName?: string;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
