
/* 
 *  Base entity includes common fields for all entities (persistence objects) in the system: id, create and update time
*/
export class BaseEntity {
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    constructor(id?: string, createdOn?: number, updatedOn?: number) { 
        this.id = id;
        this.createdOn = createdOn;
        this.updatedOn = updatedOn;
    }

}


/*
 *  Interface of BaseEntity
*/
export interface IBaseEntity {
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
