import { AccountRoleCode } from '../enums/AccountRoleCode';
import { MemberStatusCode } from '../enums/MemberStatusCode';

/* 
*/
export class MembersFindRequest {
 
    // Filter by account id 
    public accountId: string;
 
    // Filter by partial user name or email 
    public search: string;
 
    // Filter by account role(s) 
    public role: AccountRoleCode[];
 
    // Filter by member status(es) 
    public status: MemberStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(accountId?: string, search?: string, role?: AccountRoleCode[], status?: MemberStatusCode[], sort?: string, page?: number, pageSize?: number) { 
        this.accountId = accountId;
        this.search = search;
        this.role = role;
        this.status = status;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


/*
 *  Interface of MembersFindRequest
*/
export interface IMembersFindRequest {
 
    // Filter by account id 
    accountId?: string;
 
    // Filter by partial user name or email 
    search?: string;
 
    // Filter by account role(s) 
    role?: AccountRoleCode[];
 
    // Filter by member status(es) 
    status?: MemberStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    sort?: string;
 
    // Page number (for pagination) 
    page?: number;
 
    // Number of items per page (for pagination) 
    pageSize?: number;
 
}
