import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';
import { UserGenderCode } from '../enums/UserGenderCode';
import { BaseEntity } from '../entities/BaseEntity';
import { IBaseEntity } from '../entities/BaseEntity';

/* 
 *  User type
*/
export class User extends BaseEntity {
 
    // User name 
    public name: string;
 
    // User email 
    public email: string;
 
    // User mobile phone number (for notification and validation) 
    public mobile: string;
 
    // Password salt 
    public salt: string;
 
    // Password hash 
    public hash: string;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    public type: UserTypeCode;
 
    // User status: UNDEFINED | PENDING | ACTIVE | BLOCKED | SUSPENDED 
    public status: UserStatusCode;
 
    // When user was last suspended [Epoch milliseconds Timestamp] 
    public suspendedOn: number;
 
    // Number of Sign-in Attempts 
    public signInAttempts: number;
 
    // User must change password on next login 
    public changePassword: boolean;
 
    // When user created his password [Epoch milliseconds Timestamp] 
    public passwordChangedOn: number;
 
    // Default account Id 
    public defaultAccount: string;
 
    // User description 
    public description: string;
 
    // User birthday (to determine age) [Epoch milliseconds Timestamp] 
    public birthday: number;
 
    // User gender 
    public gender: UserGenderCode;
 
    // User weight [Kg] 
    public weight: number;
 
    // User Height [Cm] 
    public height: number;
 
    // User Shoe Size [European size] 
    public shoeSize: number;
 
    // Entity Id 
    public id: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


/*
 *  Interface of User
*/
export interface IUser extends IBaseEntity {
 
    // User name 
    name?: string;
 
    // User email 
    email?: string;
 
    // User mobile phone number (for notification and validation) 
    mobile?: string;
 
    // Password salt 
    salt?: string;
 
    // Password hash 
    hash?: string;
 
    // User type: UNDEFINED | SYSADMIN | SUPPORT | USER 
    type?: UserTypeCode;
 
    // User status: UNDEFINED | PENDING | ACTIVE | BLOCKED | SUSPENDED 
    status?: UserStatusCode;
 
    // When user was last suspended [Epoch milliseconds Timestamp] 
    suspendedOn?: number;
 
    // Number of Sign-in Attempts 
    signInAttempts?: number;
 
    // User must change password on next login 
    changePassword?: boolean;
 
    // When user created his password [Epoch milliseconds Timestamp] 
    passwordChangedOn?: number;
 
    // Default account Id 
    defaultAccount?: string;
 
    // User description 
    description?: string;
 
    // User birthday (to determine age) [Epoch milliseconds Timestamp] 
    birthday?: number;
 
    // User gender 
    gender?: UserGenderCode;
 
    // User weight [Kg] 
    weight?: number;
 
    // User Height [Cm] 
    height?: number;
 
    // User Shoe Size [European size] 
    shoeSize?: number;
 
    // Entity Id 
    id?: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    createdOn?: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    updatedOn?: number;
 
}
