import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
 *  Account role represents the role of the user in the account
*/
export class AccountRole {
 
    // Account Id 
    public accountId: string;
 
    // User role in the account 
    public role: AccountRoleCode;
 
    constructor(accountId?: string, role?: AccountRoleCode) { 
        this.accountId = accountId;
        this.role = role;
    }

}


/*
 *  Interface of AccountRole
*/
export interface IAccountRole {
 
    // Account Id 
    accountId?: string;
 
    // User role in the account 
    role?: AccountRoleCode;
 
}
