import { Account } from '../entities/Account';
import { LoginData } from '../common/LoginData';
import { FeatureCode } from '../enums/FeatureCode';

/* 
 *  User Account info (returned by switch-account method)
*/
export class UserAccountInfo {
 
    // Account data 
    public account: Account;
 
    // Login data 
    public loginData: LoginData;
 
    // List of features enabled for this account 
    public features: FeatureCode[];
 
    constructor(account?: Account, loginData?: LoginData, features?: FeatureCode[]) { 
        this.account = account;
        this.loginData = loginData;
        this.features = features;
    }

}


/*
 *  Interface of UserAccountInfo
*/
export interface IUserAccountInfo {
 
    // Account data 
    account?: Account;
 
    // Login data 
    loginData?: LoginData;
 
    // List of features enabled for this account 
    features?: FeatureCode[];
 
}
