
/* 
 *  @WebSocketMessage Message header for all web socket messages
*/
export class WebSocketMessageHeader {
 
    // 
    public op: number;
 
    // 
    public ver: number;
 
    // 
    public id: number;
 
    // 
    public cid: number;
 
    // 
    public sid: string;
 
    constructor(opcode?: number, version?: number, messageId?: number, correlationId?: number, sessionId?: string) { 
        this.op = opcode;
        this.ver = version;
        this.id = messageId;
        this.cid = correlationId;
        this.sid = sessionId;
    }

}


/*
 *  Interface of WebSocketMessageHeader
*/
export interface IWebSocketMessageHeader {
 
    // 
    op?: number;
 
    // 
    ver?: number;
 
    // 
    id?: number;
 
    // 
    cid?: number;
 
    // 
    sid?: string;
 
}
