
/* 
 *  Time frame (for search and reports)
*/
export class TimeFrame {
 
    // Start time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // End time [Epoch milliseconds Timestamp] 
    public to: number;
 
    constructor(from?: number, to?: number) { 
        this.from = from;
        this.to = to;
    }

}


/*
 *  Interface of TimeFrame
*/
export interface ITimeFrame {
 
    // Start time [Epoch milliseconds Timestamp] 
    from?: number;
 
    // End time [Epoch milliseconds Timestamp] 
    to?: number;
 
}
